<template>
  <div class="box">
    <iframe class="web" :src="src" frameborder="0"></iframe>
  </div>
</template>

<script>
export default {
  name:"Web",
  data(){
    return {
      src:''
    }
  },
  mounted(){
    const link = sessionStorage.getItem("webLink");
    this.src = link;
  }
}
</script>

<style scope>
.box{
  width: 100vw;
  height: 100vh;
}
.web{
  width: 100%;
  height: 100%;
}
</style>